<template>
    <!-- 登录 -->
    <div id="login">
        <div class="login-form">
            <div class="login-inner">
                <h2>
                    <p>考生登录</p>
                    <p class="f14">LOGIN</p>
                </h2>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
                    <el-form-item prop="account">
                        <el-input v-model="ruleForm.account" placeholder="用户账号"></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password"
                            placeholder="用户密码"
                            v-model="ruleForm.password"></el-input>
                    </el-form-item>
                    <div class="remberme">
                        <el-checkbox v-model="checked">记住我</el-checkbox>
                        <router-link to="/Login/eFindAccount" class="findUser">忘记密码?</router-link>
                    </div>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="toRegister">用户注册</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            ruleForm:{
                account:'',
                password:''
            },
            rules:{
                account:[
                { required:true,message:'请输入用户账号',trigger:'blur'}
                ],
                password:[
                { required:true,message:'请输入用户密码',trigger:'blur'},
                { min:6,max:12,message:'密码长度为6至16',trigger:'blur'}
                ]
            },
            checked:false
        }
    },
    methods:{
        submitForm(formVal){
            let that=this;
            this.$refs[formVal].validate((valid)=>{
                if(valid){
                    that.$api.login({
                        account:that.ruleForm.account,
                        password:that.ruleForm.password,
                        rememberMe:that.checked
                    }).then(res=>{
                        if(res.err_code === 101){
                            // that.$message('账号或密码错误,请重新输入');
                            that.ruleForm.password='';
                        }else if(res.err_code === 200){
                            sessionStorage.setItem('TOKEN','Bearer '+res.data.token);
                            that.$message('登录成功');
                            that.$router.push('/Index/User');
                        }else{
                            that.$message('登录失败');
                        }
                    })
                }else{
                return that.$message('输入存在错误,无法登录');
                }
            })
        },
        toRegister(){
            this.$router.push('/Register')
        }
    }
}
</script>

<style lang="scss" scoped>
    #login{
        width: 800px;
        padding: 50px 0;
        margin: auto;

        .login-form{
            margin: 0 auto;
            width: 420px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;
            padding: 10px;
            background: #fff;
            
            .login-inner{
              width: 100%;
              height: 100%;
              border: 1.2px solid #4579c7;
              padding: 20px 26px;
              box-sizing: border-box;

              .el-button{
                width: 100%;
              }
              h2{
                font-weight: 500;
                border-bottom: 1px dashed #333;
                text-align: left;
                p.f14{
                  font-weight: 800;
                  font-size: 14px;
                  color: #d5d5d5;
                }
              }
            }
            .el-form{
              margin-top: 10px;
            }
           
            .remberme{
                margin: 20px;
                .el-checkbox {
                    // float: left;
                }
                a{
                    float: right;
                    text-decoration: underline;
                }
            }
        }
    }
</style>